html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  box-sizing: border-box;
  border: none;
}

.container {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  min-height: 100vh;
}

.sidebar {
  width: 250px;
  box-shadow: 0px 0px 8px 0px #95a3a7b8;
}

.content {
  width: calc(100% - 250px);
  padding: 20px;
}

.wrapper {
  background-color: #fff;
  box-shadow: 1px 1px 8px 0 #95a3a7b8;
  margin-bottom: 50px;
}

.vertical .listContainer {
  max-height: 350px;
  overflow: auto;
}

.horizontal  .listContainer {
  display: flex;
  padding: 15px;
  justify-content: space-evenly;
}

.demoPageTitle {
  margin-bottom: 20px;
}

.customListItem {
  padding: 15px 40px;
  background: #fff;
  box-shadow: 1px 2px 8px 2px #eee;
}

.customTemplate {
  display: flex;
}

.btn {
  margin-left: auto;
  padding: 4px 8px;
  background-color: #eee;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.exptext {
  padding: 4px 4px 20px;
  line-height: 20px;
  letter-spacing: 0.5px;
}
